@Component({
    selector: 'page-modal',
    templateUrl: 'modal.html',
})
export class ModalPage {
    check: boolean;
    ASSET: any;
    amount: any;
    price: any;
    uid: any;
    amount1: any;
    show: boolean;
    price1: any;
    id: any;
    left: any;
    maxoffer: any;
    minoffer: any;
    constructor(public navCtrl: NavController,public translate: TranslateService, public Alert: AlertController, public toastCtrl: ToastController, public loadingCtrl: LoadingController, public viewCtrl: ViewController, public navParams: NavParams, public user: UserProvider) {
        this.translate.setDefaultLang('en');
		this.translate.use('es');
    }
    public cancel() {
        this.viewCtrl.dismiss();
    }
    makeoffer() {

        console.log(this.price1);
        if (this.price1 != '') {
            if (this.price1 > this.maxoffer) {
                var more = "more"
                this.translate.get('orderBuy-model-page.list.alert', {value: this.price1, value1: this.price, moreless:more}).subscribe((translated: string) => {
                    // console.log(translated);
                    // //=> 'Hello world'
                
                    // You can call instant() here
                   
                    var message = this.translate.instant('orderBuy-model-page.list.alert.message',{value: this.price1, value1: this.price, moreless: more});
                    var continue_btn = this.translate.instant('orderBuy-model-page.list.alert.continue_btn');
                    var cancel_btn = this.translate.instant('orderBuy-model-page.list.alert.cancel_btn');
                let alert = this.Alert.create({
                    title: '',
                    message: message,
                    buttons: [
                        {
                            text: continue_btn,
                            role: 'yes',
                            handler: () => {
                                this.makeOffer1();

                            }
                        }
                    ]
                });
                alert.present();
            })
            }

            else if(this.price1 < this.minoffer){

                var more = "less"
                this.translate.get('orderBuy-model-page.list.alert', {value: this.price1, value1: this.price, moreless:more}).subscribe((translated: string) => {
                    // console.log(translated);
                    // //=> 'Hello world'
                
                    // You can call instant() here
                   
                    var message = this.translate.instant('orderBuy-model-page.list.alert.message',{value: this.price1, value1: this.price, moreless: more});
                    var continue_btn = this.translate.instant('orderBuy-model-page.list.alert.continue_btn');
                    var cancel_btn = this.translate.instant('orderBuy-model-page.list.alert.cancel_btn');
                let alert = this.Alert.create({
                    title: '',
                    message: message,
                    buttons: [
                        {
                            text: continue_btn,
                            role: 'yes',
                            handler: () => {
                                this.makeOffer1();

                            }
                        }
                    ]
                });
                alert.present();
            })

            }
            else {
                this.show = false;
                this.makeOffer1();

            }
        } else if (this.price1 == '' || this.amount1 == '') {
            this.show = true;

        }
       
    }


    makeOffer1(){
        this.translate.get('orderBuy-model-page.list.loader').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'
        
            // You can call instant() here
            var loader = this.translate.instant('orderBuy-model-page.list.loader');
        let loading = this.loadingCtrl.create({
            content: loader
        });
        loading.present();
        this.user.makeBid(this.uid, this.ASSET, this.amount1, this.price1).subscribe(
            data => {


                let datas = JSON.parse(JSON.stringify(data));
                console.log(datas);

                if (datas.success) {

                    this.translate.get('orderBuy-model-page.list.toast5').subscribe((translated: string) => {
                        // console.log(translated);
                        // //=> 'Hello world'
                    
                        // You can call instant() here
                        var toast5 = this.translate.instant('orderBuy-model-page.list.toast5');

                    let toast = this.toastCtrl.create({
                        message: toast5,
                        duration: 3000,
                        position: 'top'
                    });
                    toast.present();

                    toast.onDidDismiss(() => {

                        this.cancel();

                    });
                })

                }

                // this.check = this.user.checkDataEmpty(datas.data);

                // console.log(this.check);
                // if (this.check) {
                //     this.navCtrl.setRoot('page-general_error');

                // } else {

                //     // this.data = datas.data;
                //     console.log(datas.data);
                //     // this.length = datas.data.length;


                // }


            },
            error => {
                console.log(error.JSON);
                setTimeout(() => {
                    loading.dismiss();
                    this.navCtrl.setRoot('page-general_error');
                }, 1000);
            },
            () => {


                loading.dismiss();


            }
        )

        })
    }


    

    ionViewDidLoad() {

        console.log('ionViewDidLoad ModalPage');
        console.log(this.navParams.get('Pair'));
        this.ASSET = this.navParams.get('Pair');
        this.amount = this.navParams.get('Amount');
        this.price = this.navParams.get('Price');
        this.uid = this.navParams.get('UID');
        this.id = this.navParams.get('id');
        this.left = this.navParams.get('left');
        // debugger;

        this.maxoffer = (Number(this.price) * 0.3) + Number(this.price);
        this.minoffer = Number(this.price) - ((this.price) * 0.3);
        console.log(this.maxoffer, this.minoffer);


        console.log(this.id, this.left, this.ASSET, this.price, this.uid, this.amount);

    }

    codeInput(event) {

        if (this.amount1 <= Number(this.left)) {
            this.show = false;


        } else {
            this.show = true;
            this.translate.get('orderBuy-model-page.list.toast6').subscribe((translated: string) => {
                // console.log(translated);
                // //=> 'Hello world'
            
                // You can call instant() here
                var toast6 = this.translate.instant('orderBuy-model-page.list.toast6');
            let toast = this.toastCtrl.create({
                message: toast6,
                duration: 3000,
                position: 'top'
            });
            toast.present();

            toast.onDidDismiss(() => {



            });

        })
        }



        if (this.price1 == '' || this.amount1 == '') {
            this.show = true;

        }

    }

    codeInput1(event) {

        console.log(this.price1);
        if (this.price1 != '') {
            if (this.price1 > this.maxoffer || this.price1 < this.minoffer) {
                this.translate.get('orderBuy-model-page.list.alert', {value: this.price1, value1: this.price}).subscribe((translated: string) => {
                    // console.log(translated);
                    // //=> 'Hello world'
                
                    // You can call instant() here
                    var message = this.translate.instant('orderBuy-model-page.list.alert.message',{value: this.price1, value1: this.price});
                    var continue_btn = this.translate.instant('orderBuy-model-page.list.alert.continue_btn');
                    var cancel_btn = this.translate.instant('orderBuy-model-page.list.alert.cancel_btn');
                let alert = this.Alert.create({
                    title: '',
                    message: message,
                    buttons: [
                        {
                            text: continue_btn,
                            role: 'yes',
                            handler: () => {
                                this.makeoffer();

                            }
                        },
                        {
                            text: cancel_btn,
                            handler: () => {
                                this.show = false;

                            }
                        }
                    ]
                });
                alert.present();
            })
            }
            else {
                this.show = false;
            }
        } else if (this.price1 == '' || this.amount1 == '') {
            this.show = true;

        }



    }
}




